<template>
    <div :class="isShowList?'ToppedList_box active':'ToppedList_box'"
        ref='section'
        @touchstart='touchstart'
        @touchmove='touchmove'
        @touchend='touchend'
    >
        <ul v-for='(item,index) in ToppedList' :key='index'>
            
            <h6>{{item.GroupName}}</h6>
            <div v-for='(val,key) in item.GroupList' :key='key' class='list'>
                    <router-link :to='{path:"/detail",query:{id:val.SerialID}}' class='left'> 
                        <img :data-src="val.Picture" src='../../assets/lazyload.jpg'>
                    </router-link>
                        <div class='right'>
                            <p>{{val.AliasName}}</p>
                            <span>{{val.DealerPrice}}</span>
                        </div>
                    
            </div>

           
        </ul>
    </div>
</template>
<script>
    import {
        mapState,
        mapMutations
    } from 'vuex'
    import lazyLoad from '../../util/lazyLoad.js'
    export default {
        computed: {
            ...mapState({
                ToppedList: state => state.index.ToppedList,
                isShowList: state => state.index.isShowList
            })
        },
        methods: {
            ...mapMutations({
                hideMakeList: 'index/hideMakeList'
            }),
            touchstart(e) {
                this.touch = e.touches[0]
            },
            touchmove(e) {
                let touch = e.touches[0];
                this.offsetX = touch.pageX - this.touch.pageX
                console.log(this.offsetX)
                if (this.offsetX > 0) {
                    this.$refs.section.style = `transform:translate3d(${this.offsetX}px,0,0)`
                    console.log(this.$refs.section)
                }
            },
            touchend(e) {
                this.$refs.section.style = ``
                if (this.offsetX > 100) {
                    this.hideMakeList();
                }
            }
        },
        updated() {
            lazyLoad('.ToppedList_box')
        }
    }
</script>
<style scoped lang='scss'>
    .ToppedList_box {
        position: fixed;
        top: 0;
        right: 0;
        width: 75%;
        height: 100%;
        overflow: scroll;
        background: #fff;
        transform: translate3d(100%, 0, 0);
    }
    
    h6 {
        background: #eee;
        height: .4rem;
        line-height: .4rem;
        color: #666;
        font-weight: 400;
        text-indent: .1rem;
        font-size: .28rem;
    }
    
    .list {
        display: flex;
        border-bottom: 1px solid #ccc;
        height: 1.4rem;
        color: #585858;
        .left {
            width: 30%;
            padding: 0 .1rem;
            line-height: 1.4rem;
            img {
                width: 100%;
            }
        }
        .right {
            flex: 1;
            font-size: .24rem;
            margin-top: .3rem;
            p {
                line-height: 1.5;
            }
            span {
                line-height: 2;
                color: red;
            }
        }
    }
    
    .ToppedList_box.active {
        transform: translate3d(0, 0, 0);
        transition: transform .3s linear;
    }
</style>


