<template>
  <div class='warp'>
    <div class='scroll_box'>
        <section>
            <div v-for='(item,index) in MotorcycleType' :key='index'>
              <ul>
                  <h6  :id='index'>{{index}}</h6>
                  <li v-for='(val,key) in item' :key='key' @click='ToppedList(val.MasterID)'>
                    <img :data-src='val.CoverPhoto' src="../assets/lazyload.jpg">
                    <span>{{val.Name}}</span>
                  </li>
              </ul>
            </div>
           
          </section>
    </div>
    <div class='right'>
      <span 
            @touchstart='touchstart'
            @touchmove='touchmove'
            @touchend='touchend'
      v-for='(item,index) in Acronyms' :key='index'>
        <!-- <a :href="'#'+item">{{item}}</a> -->
        {{item}}
      </span>
    </div>
    <div class="mask" v-if="isShow">{{letter!==""?letter:null}}</div>
    <MakeList></MakeList>
</div>
  
</template>

<script>
    import {
        mapState,
        mapActions,
        mapMutations
    } from 'vuex'
    import MakeList from './common/MakeList'
    import lazyLoad from '../util/lazyLoad.js'
    export default {
        data() {
            return {
                letter: ""
            }
        },
        computed: {
            ...mapState({
                Acronyms: state => state.index.Acronyms,
                MotorcycleType: state => state.index.MotorcycleType,
                isShow: state => state.index.isShow
            }),

        },
        components: {
            MakeList
        },
        methods: {
            ...mapActions({
                initState: 'index/initState',
                ToppedList: 'index/ToppedList'
            }),

            touchmove(e) {
                //console.log(e.touches)
                //获取手指的位置
                let pageY = e.touches[0].pageY
                    //console.log(pageY)
                    //计算当前在哪一个字母
                let index = Math.floor((pageY - this.marginTop) / this.height)

                if (index < 0) {
                    index = 0
                } else if (index > this.Acronyms.length - 1) {
                    index = this.Acronyms.length - 1
                }
                //console.log(this.Acronyms[index]);
                this.letter = this.Acronyms[index];
                let ele = document.getElementById(this.Acronyms[index])
                    //console.log(ele)
                let top = ele.offsetTop;
                // 获取字母列表距离顶部的高度
                document.querySelector('.scroll_box').scrollTop = top;
            },
            touchend(e) {
                this.letter = ''
            },
            ...mapMutations({
                touchstart: "index/touchstart",
                touchend: "index/touchend"
            })
        },
        mounted() {
            this.initState()
        },
        updated() {
            //获取每个字母的高度
            this.height = 0.4 * window.innerWidth / 750 * 100
                //console.log(this.height)
                // 获取字母列表距离顶部的高度
            this.marginTop = (window.innerHeight - (this.Acronyms.length) * this.height) / 2;
            lazyLoad('.scroll_box')
        },
    }
</script>


<style scoped lang='scss'>
    .warp {
        width: 100%;
        height: 100%;
    }
    
    .scroll_box {
        width: 100%;
        height: 100%;
        overflow: scroll;
    }
    
    .right {
        position: fixed;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #ccc;
        border-radius: .2rem;
        span {
            color: #666;
            font-size: .24rem;
            text-align: center;
            line-height: .4rem;
        }
    }
    
    ul {
        h6 {
            background: #eee;
            height: .4rem;
            line-height: .4rem;
            color: #666;
            font-weight: 400;
            text-indent: .1rem;
            font-size: .28rem;
        }
        li {
            margin: 0 .3rem;
            border-bottom: 1px solid #ccc;
            height: 1rem;
            display: flex;
            align-items: center;
            img {
                width: .8rem;
                height: .8rem;
            }
            span {
                display: block;
                line-height: .5rem;
                padding: 0 .5rem;
                font-size: .32rem;
            }
        }
    }
    
    .mask {
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%);
        width: 1.5rem;
        height: 1.5rem;
        background: rgba(0, 0, 0, .8);
        color: #fff;
        text-align: center;
    }
</style>


